.modal-table {
  //   position: absolute;
  //   top: 0;
  //   left: 0;
  
  margin: 0 auto;
  max-width: 414px;
  width: 100%;
  height: auto;
  padding: 60px 15px;
  background: #fffdee;
  border-radius: 2px;
}

.modal-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal-table-title {
  display: block;
  width: 100%;
  font-family: 'Times New Roman';
  font-weight: 700;
  font-size: 24px;
  line-height: calc(28 / 24);
  letter-spacing: 0.05em;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.modal-table-group {
  width: 100%;
  max-width: 384px;
  margin: 0 auto;
  margin-bottom: 45px;
}

.modal-table-field {
  position: relative;
}

.modal-table-field.select::after {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  content: '';
	display: inline-block;
  background-image: url(/src/images/modal/ep_arrow-down-bold.svg);
	width: 24px;
	height: 24px;
}

.modal-table-field:not(:last-child) {
  margin-bottom: 45px;
}

.modal-table-list {
  max-width: 384px;
  width: 100%;
  list-style: none;
}

.modal-input {
  display: block;
  max-width: 384px;
  width: 100%;
  height: 54px;
  padding-left: 12px;
  font-family: 'Times New Roman';
  font-weight: 700;
  font-size: 20px;
  line-height: calc(23 / 20);
  letter-spacing: 0.05em;
  color: #808080;
  background: transparent;
  border: 1px solid #c79002;
  border-radius: 2px;
  outline: #c79002;
}

.contact-group {
  margin-bottom: 45px;
}

.modal-form-label {
  position: absolute;
  top: 62px;
  left: 12px;
  color: #c79002;
}

.table-details {
  margin-bottom: 40px;
}

.modal-table-select {
  appearance: none;
  background-color: transparent;
  border: 1px solid #c79002;
  border-radius: 2px;
  width: 100%;
  max-width: 384px;
  height: 54px;
  padding-left: 12px;
  font-family: 'Times New Roman';
  font-weight: 700;
  font-size: 20px;
  line-height: calc(23 / 20);
  letter-spacing: 0.05em;
  color: #808080;
  cursor: inherit;
  outline: none;
}

.modal-btn-confirm {
  display: block;
  width: 100%;
  max-width: 384px;
  height: 64px;
  margin: 0 auto;
  margin-top: 20px;
  font-family: 'Montserrat';
  font-weight: 500;
  font-size: calc(10px + 1.1vw);
  line-height: calc(24 / 20);
  color: #ffffff;
  text-transform: uppercase;
  background: #000000;
  border: 1px solid #f3c653;
  border-radius: 2px;
  cursor: pointer;
  transition-property: background;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-btn-confirm:hover,
.modal-btn-confirm:focus {
  background: #f3c653;
}

@media screen and (min-width: 360px) {
  .modal-btn-confirm {
    font-size: calc(12px + 1.1vw);
  }
}

@media screen and (min-width: 414px) {
  .modal-btn-confirm {
    font-size: 19px;
  }
}

@media screen and (min-width: 1024px) {
  .modal-table {
    padding: 60px;
    max-width: 712px;
  }

  .modal-table-title {
    font-size: 40px;
    line-height: calc(46 / 40);
  }

  .modal-table-list {
    display: flex;
    max-width: 592px;
  }

  .table-list-item:not(:last-child) {
    margin-right: 16px;
  }

  .modal-table-group {
    max-width: 288px;
  }

  .table-details {
    width: 288px;
  }

  .modal-table-field:not(:last-child) {
    margin-bottom: 56px;
  }

  .modal-input {
    max-width: 288px;
    height: 64px;
    font-size: 24px;
    line-height: calc(28 / 24);
  }

  .modal-form-label {
    position: absolute;
    top: 72px;
  }

  .modal-table-select {
    height: 64px;
    font-weight: 700;
    font-size: 24px;
    line-height: calc(28 / 24);
  }

  .modal-btn-confirm {
    max-width: 592px;
    font-size: 24;
    line-height: calc(29 / 24);
  }
}
